:root {
  font-family: "Hind", Arial, sans-serif;
  line-height: 1.6;
}

:root * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  font-family: "Hind", Arial, sans-serif;
}

.wrapper .icon {
  transition: all .2s ease;
}

.wrapper a {
  transition: all 0.2s ease;
  color: inherit;
}

.wrapper a:hover {
  text-decoration: none;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.bold-text {
  font-weight: bold;
}

.account__avatar {
  width: 37px;
  height: 37px;
  margin-right: 13px;
  border-radius: 50%;
  overflow: hidden;
}

.account__avatar img {
  display: block;
  width: 100%;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 74px;
  padding-left: 30px;
  padding-right: 20px;
  background-color: #28292d;
}

@media only screen and (max-width: 480px) {
  .header {
    flex-direction: column;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.header__left {
  display: flex;
  align-items: baseline;
  padding-bottom: 5px;
}

@media only screen and (max-width: 768px) {
  .header__left {
    flex-direction: column;
  }
}

@media only screen and (max-width: 480px) {
  .header__left {
    margin-bottom: 15px;
  }
}

.header__logo {
  margin-right: 21px;
}

.header__logo-link {
  width: 121px;
  height: 28px;
}

.header__title {
  padding: 0;
  margin: 0;
}

.icon-logo {
  height: 28px;
  width: 121px;
}

.header__title {
  font-size: 13.1px;
  color: #888888;
}

.header__navbar {
  display: flex;
  align-items: center;
  color: #999999;
}

@media only screen and (max-width: 768px) {
