    padding-top: 15px;
    padding-bottom: 10px;
    flex-direction: column;
  }
}

.header__notification {
  display: flex;
  align-items: center;
  padding-top: 10px;
}

.navbar__item {
  display: flex;
  align-items: center;
}

.navbar__icon {
  position: relative;
  padding: 0;
  margin: 0;
}

.navbar__circle {
  position: absolute;
  text-align: center;
  align-items: center;
  display: inline-block;
  width: 16px;
  height: 16px;
  font-size: 9.7px;
  line-height: 18px;
  font-weight: bold;
  color: #fff;
  border-radius: 50%;
  background-color: #9caa49;
  transition: all .2s ease;
}

.navbar__circle--green {
  left: -8px;
  top: -10px;
}

.navbar__circle--green:hover {
  background-color: #acba5d;
}

.navbar__circle--red {
  left: -9px;
  top: -7px;
  background-color: #ff7e1b;
}

.navbar__circle--red:hover {
  background-color: #ff923f;
}

.icon-email {
  width: 20px;
  height: 15px;
}

.navbar__inbox {
  margin-right: 17px;
}

.navbar__inbox .navbar__text {
  margin-left: 7px;
}

.navbar__text {
  font-size: 13px;
  line-height: 1.2;
  letter-spacing: -0.3px;
  transition: color 0.2s ease;
}

.navbar__text:hover {
  color: #fff;
}

.navbar__alert {
  margin-right: 26px;
}

@media only screen and (max-width: 480px) {
  .navbar__alert {
    margin-right: 0;
  }
}

.icon-alert {
  width: 22px;
  height: 18px;
}

