.main {
  position: relative;
  display: flex;
  background-color: #fafafa;
}

@media only screen and (max-width: 480px) {
  .main {
    height: 1200px;
  }
}

.main__navbar {
  position: relative;
  display: flex;
  min-height: calc(100vh - 74px);
  padding-top: 45px;
  padding-bottom: 45px;
  color: #ececec;
  background-color: #5c5f69;
}

.main__navbar:hover .secondmenu {
  width: 170px;
}

.main__navbar:hover:after {
  content: "";
  position: absolute;
  top: 0;
  left: 78px;
  display: block;
  height: 100%;
  width: 170px;
  background-color: #5c5f69;
  z-index: 1;
}

.main__navbar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  width: 78px;
  background-color: #3a3c44;
  z-index: 1;
}

@media only screen and (max-width: 768px) {
  .main__navbar:before {
    width: 50px;
  }
}

.menu {
  z-index: 10;
}

.menu__item {
  display: flex;
  transition: all 0.2s ease;
}

.menu__item:hover .menu__icon {
  border-color: #9caa49;
}

.menu__item:hover .secondmenu__title {
  color: #ebebeb;
  background-color: #9caa49;
}

.menu__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 78px;
  height: 44px;
  background-color: #3a3c44;
  border-left: 4px solid transparent;
}

@media only screen and (max-width: 768px) {
  .menu__icon {
    width: 50px;
  }
}

.icon-projects {
