}

.secondmenu {
  width: 0;
  padding-left: 0px;
  overflow: hidden;
  transition: all 0.2s linear;
  background-color: #5c5f69;
  transition: all 0.4s ease;
}

.secondmenu:hover {
  height: 100%;
}

.secondmenu:hover .secondmenu__list {
  height: 100%;
}

.secondmenu__title {
  position: relative;
  display: flex;
  align-items: center;
  height: 44px;
  padding-left: 24px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.57;
}

.secondmenu__title .chevron {
  position: absolute;
  top: 12px;
  left: 10px;
  display: inline-block;
}

.icon-chevron-right {
  width: 4px;
  height: 8px;
}

.secondmenu__list {
  height: 0;
  padding-top: 5px;
  padding-right: 18px;
  overflow: hidden;
  transition: all 0.4s ease;
}

.secondmenu__link {
  display: inline-block;
  padding: 5px 18px 5px 24px;
  font-size: 14px;
  white-space: nowrap;
}

.secondmenu__link:hover {
  color: #999933;
}

.main__content {
