@import "reset";
@import "fonts";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  font-family: $font-primary;
  .icon {
    transition: all .2s ease;
  }
  a {
    transition: all 0.2s ease;
    color: inherit;
    &:hover {
      text-decoration: none;
    }
  }
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.bold-text {
  font-weight: bold;
}

.account__avatar {
  width: 37px;
  height: 37px;
  margin-right: 13px;
  border-radius: 50%;
  overflow: hidden;
  img {
    display: block;
    width: 100%;
  }
}

// HEADER
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 74px;
  padding-left: 30px;
  padding-right: 20px;
  background-color: #28292d;
  @media only screen and (max-width: 480px) {
    flex-direction: column;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.header__left {
  display: flex;
  align-items: baseline;
  padding-bottom: 5px;
  @media only screen and (max-width: 768px) {
    flex-direction: column;
  }
  @media only screen and (max-width: 480px) {
    margin-bottom: 15px;
  }
}

.header__logo {
  margin-right: 21px;
}

.header__logo-link {
  width: 121px;
  height: 28px;
}

.header__title {
  padding: 0;
  margin: 0;
}

.icon-logo {
  height: 28px;
  width: 121px;
}

.header__title {
  font-size: 13.1px;
  color: #888888;
}

.header__navbar {
  display: flex;
  align-items: center;
  color: #999999;
  @media only screen and (max-width: 768px) {
    padding-top: 15px;
    padding-bottom: 10px;
    flex-direction: column;
  }
}

.header__notification {
  display: flex;
  align-items: center;
  padding-top: 10px;
}

.navbar__item {
  display: flex;
  align-items: center;
}

.navbar__icon {
  position: relative;
  padding: 0;
  margin: 0;
}

.navbar__circle {
  position: absolute;
  text-align: center;
  align-items: center;
  display: inline-block;
  width: 16px;
  height: 16px;
  font-size: 9.7px;
  line-height: 18px;
  font-weight: bold;
  color: #fff;
  border-radius: 50%;
  background-color: #9caa49;
  transition: all .2s ease;
}

.navbar__circle--green {
  left: -8px;
  top: -10px;
  &:hover {
    background-color: lighten(#9caa49, 7%);
  }
}

.navbar__circle--red {
  left: -9px;
  top: -7px;
  background-color: #ff7e1b;
  &:hover {
    background-color: lighten(#ff7e1b, 7%);
  }
}

.icon-email {
  width: 20px;
  height: 15px;
}

.navbar__inbox {
  margin-right: 17px;
  .navbar__text {
    margin-left: 7px;
  }
}

.navbar__text {
  font-size: 13px;
  line-height: 1.2;
  letter-spacing: -0.3px;
  transition: color 0.2s ease;
  &:hover {
    color: #fff;
  }
}

.navbar__alert {
  margin-right: 26px;
  @media only screen and (max-width: 480px) {
    margin-right: 0;
  }
}

.icon-alert {
  width: 22px;
  height: 18px;
}

.navbar__account {
  display: flex;
  align-items: center;
  padding-left: 16px;
  border-left: 1px solid #444444;
  @media only screen and (max-width: 768px) {
    margin-top: 15px;
    border-left: none;
  }
  @media only screen and (max-width: 480px) {
    padding-left: 0;
  }
}

.account__hello {
  font-size: 16px;
  line-height: 1.2;
  text-align: left;
  color: #9caa49;
}

.account__link {
  display: flex;
  align-items: center;
  .navbar__text {
    margin-right: 6px;
    font-size: 11px;
    line-height: 1.5;
    font-weight: 500;
    letter-spacing: 0;
  }
}

.icon-settings {
  width: 13px;
  height: 14px;
}

.navbar__turn-on {
  margin-left: 22px;
}

.icon-turn-on {
  width: 21px;
  height: 24px;
}

// MAIN
.main {
  position: relative;
  display: flex;
  background-color: #fafafa;
  @media only screen and (max-width: 480px) {
    height: 1200px;
  }
}

// PRIMARY MENU
.main__navbar {
  position: relative;
  display: flex;
  min-height: calc(100vh - 74px);
  padding-top: 45px;
  padding-bottom: 45px;
  color: #ececec;
  background-color: #5c5f69;
  &:hover {
    .secondmenu {
      width: 170px;
    }
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 78px;
      display: block;
      height: 100%;
      width: 170px;
      background-color: #5c5f69;
      z-index: 1;
    }
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 78px;
    background-color: #3a3c44;
    z-index: 1;
    @media only screen and (max-width: 768px) {
      width: 50px;
    }
  }
}

.menu {
  z-index: 10;
}

.menu__item {
  display: flex;
  transition: all 0.2s ease;
  &:hover {
    .menu__icon {
      border-color: #9caa49;
    }
    .secondmenu__title {
      color: #ebebeb;
      background-color: #9caa49;
    }
  }
}

.menu__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 78px;
  height: 44px;
  background-color: #3a3c44;
  border-left: 4px solid transparent;
  @media only screen and (max-width: 768px) {
    width: 50px;
  }
}

.icon-projects {
  width: 16px;
  height: 15px;
}

.icon-pencil {
  width: 20px;
  height: 19px;
}

.icon-reports {
  width: 17px;
  height: 18px;
}

.icon-agenda {
  width: 21px;
  height: 20px;
}

.icon-maps {
  width: 13px;
  height: 20px;
  fill: #dddddd;
}

.icon-schedule {
  width: 18px;
  height: 17px;
}

.secondmenu {
  width: 0;
  padding-left: 0px;
  overflow: hidden;
  transition: all 0.2s linear;
  background-color: #5c5f69;
  transition: all 0.4s ease;
  &:hover {
    height: 100%;
  }
  &:hover .secondmenu__list {
    height: 100%;
  }
}

.secondmenu__title {
  position: relative;
  display: flex;
  align-items: center;
  height: 44px;
  padding-left: 24px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.57;
  .chevron {
    position: absolute;
    top: 12px;
    left: 10px;
    display: inline-block;
  }
}

.icon-chevron-right {
  width: 4px;
  height: 8px;
}

.secondmenu__list {
  height: 0;
  padding-top: 5px;
  padding-right: 18px;
  overflow: hidden;
  transition: all 0.4s ease;
}

.secondmenu__link {
  display: inline-block;
  padding: 5px 18px 5px 24px;
  font-size: 14px;
  white-space: nowrap;
  &:hover {
    color: #999933;
  }
}

// MAIN CONTENT
.main__content {
  width: 83.5%;
  position: absolute;
  top: 0;
  left: 16.5%;
  @media only screen and (max-width: 992px) {
    left: 12%;
    padding-right: 2%;
  }
  @media only screen and (max-width: 992px) {
    left: 17%;
  }
}

.content__title {
  margin-top: 98px;
  margin-bottom: 45px;
  @include rfs(22);
  font-weight: 500;
  color: #99aa46;
  @media only screen and (max-width: 768px) {
    margin-top: 60px;
    margin-bottom: 22px;
  }
  span {
    font-weight: 300;
    color: #999999;
  }
}

.content__head {
  margin-bottom: 85px;
  display: flex;
  align-items: center;
  font-size: 16px;
  @media only screen and (max-width: 768px) {
    margin-bottom: 30px;
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

.content__box {
  padding-right: 27px;
  border-right: 1px solid #dddddd;
  @media only screen and (max-width: 480px) {
    padding-right: 0;
    border: none;
  }
}

.content__hello {
  @media only screen and (max-width: 480px) {
    margin-top: 10px;
  }
}

.content__name {
  font-weight: 600;
  color: #9caa49;
}

.content__text {
  font-size: 16px;
  color: #5c5f69;
}

.weather {
  display: flex;
  align-items: center;
  padding-left: 27px;
  @media only screen and (max-width: 480px) {
    margin-top: 10px;
    padding-left: 50px;
  }
}

.weather__box {
  line-height: 1;
  letter-spacing: 0.3px;
}

.weather__date {
  font-size: 14.8px;
  color: #9baa48;
}

.weather__forecast {
  font-size: 16.3px;
  color: #aaaaaa;
}

.weather__icon {
  margin-left: 13px;
}

.icon-weather {
  width: 44px;
  height: 32px;
}

// content blocks
.content__blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1010px;
  max-width: 1010px;
  @media only screen and (max-width: 1366px) {
    width: 100%;
  }
}

.blocks__item {
  flex-basis: 32.2%;
  max-width: 330px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 189px;
  margin-bottom: 13px;
  padding: 5.7%;
  background-color: #f2f2f2;
  @media only screen and (max-width: 768px) {
    flex-basis: 49%;
    align-items: center;
    height: 130px;
  }
  @media only screen and (max-width: 480px) {
    flex-basis: 100%;
  }
}

.icon-projects-big {
  width: 43px;
  height: 40px;
}

.icon-agenda-big {
  width: 41px;
  height: 39px;
}

.icon-schedule-big {
  width: 40px;
  height: 38px;
}

.icon-pencil-big {
  width: 43px;
  height: 39px;
}

.icon-reports-big {
  width: 36px;
  height: 38px;
}

.icon-maps-big {
  width: 25px;
  height: 40px;
  fill: #5ec2ab;
}

.block__name {
  margin-top: 15px;
  @include rfs(20);
  font-weight: 300;
  color: #5c5f69;
  @media only screen and (min-width: 1200px) {
    white-space: nowrap;
  }
}

// FOOTER 
.navbar__footer {
  position: absolute;
  bottom: 60px;
  right: -270px;
  display: flex;
  align-items: baseline;
  padding-bottom: 5px;
  @media only screen and (max-width: 480px) {
    flex-direction: column;
  }
  .footer__logo-link {
    img {
      display: block;
      width: 98px;
      height: 23px;
    }
  }
  .footer__title {
    margin-left: 12px;
  }
  .footer__title {
    font-size: 13.1px;
    color: #cfd0d6;
  }
}